import React, { Component } from "react";
import { render } from "react-dom";
import JourneyProvider from "./journeyProvider";
import "./styles.css";
import queryString from "query-string";

interface AppProps {}
interface AppState {
  name: string;
}

class App extends Component<AppProps, AppState> {
  constructor(props: any) {
    super(props);
    this.state = {
      name: "React"
    };
  }

  parsed = queryString.parse(window.location.search);

  journeyData = {
    formDefault: {
      client: this.parsed.client,
      companyId: this.parsed.companyId,
      userName: this.parsed.userName,
      uri: this.parsed.uri
    }
  };

  render() {
    return (
      <JourneyProvider
        journeyData={this.journeyData}
        sessionId={""}
        sessionReady={true}
        journeyId={"0ec7f940-4cf2-44ad-93b5-fd455c914843"}
        companyId={"44937201-8c0a-4ce3-9843-66531f9d7698"}
        host={"https://test.delta-forms-widget.sapienspaas.com"}
      />
    );
  }
}

render(<App />, document.getElementById("root"));
